
import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import { checkValueType } from 'plugin/helper'

@Component({
  name: 'comDictSelect'
})
export default class comDictSelect extends Vue {
  @Prop() value!: any
  @Prop({ type: Boolean }) disabled!: boolean
  @Prop({ type: Boolean }) multiple!: boolean
  @Prop({ type: String }) placeholder!: string
  @Prop({ type: Boolean }) collapseTags!: boolean
  @Prop({ type: Boolean, default: true }) clearable!: boolean
  @Prop({ type: String }) size!: string
  @Prop({ type: String }) remoteKey!: string
  @Prop({ type: String, default: 'default' }) valueType!: string
  @Prop({ type: Boolean, default: false }) isGroup!: boolean
  @Prop({ type: Array, default: () => [] }) permitData!: Array<string>
  @Prop({ type: Array, default: () => [] }) excludeData!: Array<string>

  componentInit = false
  insideValue: any = this.multiple ? [] : ''

  @Watch('value', { immediate: true })
  valueHandler(): void {
    if (checkValueType(this.value, '[object Undefined]') || checkValueType(this.value, '[object Null]')) {
      this.insideValue = this.multiple ? [] : ''
      return
    }
    this.insideValue = this.value
  }

  @Watch('insideValue')
  insideValueHandler() {
    this.$emit('input', this.insideValue)
  }

  get options(): Array<any> {
    return [
      { description: '必填', value: 'require' },
      { description: '姓名', value: 'name' },
      { description: '手机号码', value: 'phone' },
      { description: '身份证', value: 'idCard' },
      { description: '正则', value: 'regex' },
      { description: '接口校验', value: 'remote' }
    ]
  }
}
