
import { Vue, Component, Watch } from 'vue-property-decorator'
import { Action, Getter } from 'vuex-class'
import { routes } from '@/router'

const path = require('path')

interface affixTagsInterface {
  fullPath: string
  path: string
  name?: string
  meta: any
}

@Component({
  name: 'insideTagView'
})
export default class insideTagView extends Vue {
  @Action('view/addView') actionAddView!: (data: any) => Promise<any>
  @Action('view/addTagsView') actionAddTagsView!: (data: affixTagsInterface) => Promise<any>
  @Action('view/delView') actionDelView!: (data: any) => Promise<{ tagsViews: Array<any> }>
  @Action('view/delOthersViews') actionDelOthersViews!: (data: any) => Promise<{ tagsViews: Array<any> }>
  @Action('view/delAllViews') actionDelAllViews!: () => Promise<{ tagsViews: Array<any> }>
  @Action('view/delCacheView') actionDelCacheView!: (data: any) => Promise<{ cacheViews: Array<any> }>
  @Getter('view/tagsViews') tagsViews!: Array<any>

  contextmenu: {
    top: number
    left: number
    selectedTag: any | {}
    visible: boolean
  } = {
    top: 0,
    left: 0,
    selectedTag: {},
    visible: false
  }

  @Watch('$route')
  routeHandler(): void {
    this.addView()
  }

  @Watch('contextmenu.visible')
  visibleHandler(): void {
    if (this.contextmenu.visible) {
      document.body.addEventListener('click', this.closeMenu)
    } else {
      document.body.removeEventListener('click', this.closeMenu)
    }
  }

  // 是否激活
  isActive(route: any): boolean {
    return route.path === this.$route.path
  }

  // 是否是黏贴标签
  isAffix(tag: any | any): boolean {
    return tag.meta && tag.meta.affix
  }

  // 设置 tag item 的class
  setTagItemClass(tag: any): string {
    if (this.isActive(tag)) {
      return 'inside-tag__item inside-tag__active'
    } else {
      return 'inside-tag__item'
    }
  }

  // 跳转到最新的 tags-views
  toLastView(tagsViews: Array<any>, view: any): void {
    const latestView: any = tagsViews.slice(-1)[0]
    if (latestView) {
      this.$router.push(latestView.fullPath)
    }
  }

  // 筛选出始终需要黏贴在 tags-views 上的 tags
  filterAffixTags(routes: Array<any>, basePath: string = '/'): Array<affixTagsInterface> {
    let tags: Array<affixTagsInterface> = []
    routes.forEach((route: any) => {
      if (this.isAffix(route)) {
        const tagPath: string = path.resolve(basePath, route.path)
        tags.push({
          fullPath: tagPath,
          path: tagPath,
          name: route.name,
          meta: { ...route.meta }
        })
      }
      if (route.children) {
        const tempTags = this.filterAffixTags(route.children, route.path)
        if (tempTags.length >= 1) {
          tags = [...tags, ...tempTags]
        }
      }
    })
    return tags
  }

  // 初始化添加需要黏贴在 tags-views 上的 tags
  initAffixTags(): void {
    const affixTags: Array<affixTagsInterface> = this.filterAffixTags(routes)
    for (const tag of affixTags) {
      if (tag.name) {
        this.actionAddTagsView(tag)
      }
    }
  }

  // 添加 cache-views 和 tags-views
  addView(): void {
    const { name } = this.$route
    if (name) {
      this.actionAddView(this.$route)
    }
  }

  // 关闭一个 tag
  closeSelectedTag(view: any): void {
    this.actionDelView(view).then(({ tagsViews }) => {
      if (this.isActive(view)) {
        this.toLastView(tagsViews, view)
      }
    })
  }

  // 关闭其他 tag
  closeOthersTags(view: any): void {
    if (!this.isActive(view)) {
      this.$router.push(view as any)
    }
    this.actionDelOthersViews(view)
  }

  // 关闭所有 tag
  closeAllTags(view: any): void {
    this.actionDelAllViews().then(({ tagsViews }) => {
      this.toLastView(tagsViews, view)
    })
  }

  // 刷新 tag
  refreshSelectedTag(view: any): void {
    this.actionDelCacheView(view).then(() => {
      const { fullPath } = view
      this.$nextTick(() => {
        this.$router.replace({
          path: '/redirect',
          query: { fullPath }
        })
      })
    })
  }

  // 右键打开菜单
  openMenu(tag: any, e: MSGestureEvent): void {
    const menuMinWidth: number = 105
    const offsetLeft: number = this.$el.getBoundingClientRect().left
    const offsetWidth: number = (this.$el as HTMLElement).offsetWidth
    const maxLeft: number = offsetWidth - menuMinWidth
    const left: number = e.clientX - offsetLeft + 15

    if (left > maxLeft) {
      this.contextmenu.left = maxLeft
    } else {
      this.contextmenu.left = left
    }

    this.contextmenu.top = e.clientY
    this.contextmenu.visible = true
    this.contextmenu.selectedTag = tag
  }

  // 关闭菜单
  closeMenu(): void {
    this.contextmenu.visible = false
  }

  created(): void {
    this.initAffixTags()
    this.addView()
  }
}
