
import { Vue, Component, Watch } from 'vue-property-decorator'

@Component({
  name: 'breadcrumb'
})
export default class breadcrumb extends Vue {
  levelList: Array<any> = []

  @Watch('$route')
  routeHandler(): void {
    this.getBreadcrumb()
  }

  // 获取面包屑导航列表
  getBreadcrumb(): void {
    let matched: Array<any> = this.$route.matched.filter((item: any) => item.meta && item.meta.title)
    this.levelList = matched.filter((item) => item.meta && item.meta.title)
  }

  created() {
    this.getBreadcrumb()
  }
}
