import { isEmpty, toFixed, dateFormat } from 'plugin/helper'

import { VueConstructor } from 'vue/types/umd'

interface filtersInterface {
  [k: string]: Function
}

const filters: filtersInterface = {
  /**
   * 日期格式
   * string | format(fmt)
   * number | format(fmt)
   * Date | format(fmt)
   */
  format: (val: string | number | Date, fmt?: string): string => {
    return dateFormat(val, fmt)
  },

  /**
   * 脱敏
   * string | desensitization(start, end)
   * number | desensitization(start, end)
   */
  desensitization: (val: string | number, start: number = 4, end: number = 4): string => {
    if (isEmpty(val)) return ''
    return val.desensitization(start, end)
  },

  /**
   * 保留小数点后几位
   * string | toFixed(num)
   * number | toFixed(num)
   */
  toFixed: (val: string | number, num: number = 2): string => {
    return toFixed(val, num)
  }
}

/**
 * 启动注册全局过滤器
 * @param Vue
 */
export const setupRegisterFilter = (Vue: VueConstructor<Vue>) => {
  Object.keys(filters).forEach((key: string) => {
    Vue.filter(key, filters[key])
  })
}
