const files = require.context('./modules', true, /\.ts$/)

export default {
  install(Vue: Vue.VueConstructor) {
    files.keys().forEach((key: string) => {
      const modules = key.split('/').pop() || ''
      const modulesName: string = `$${modules.replace(/(\.ts)/g, '')}`
      Vue.prototype[modulesName] = files(key).default
    })
  }
}
