import moment from 'moment'

moment.locale('zh-cn', {
  week: {
    dow: 1,
    doy: 7
  }
})

// 日期格式化
export const dateFormat = (value: string | number | Date, format?: string) => {
  if (value && value.format) {
    return value.format(format)
  }

  return ''
}

// 获取某天开始时间
export const startDayTime = (date: Date) => {
  return date.setHours(0, 0, 0, 0).format('YYYY-MM-DD HH:mm:ss')
}

// 获取某天结束时间
export const endDayTime = (date: Date) => {
  return date.setHours(23, 59, 59, 59).format('YYYY-MM-DD HH:mm:ss')
}

// 获取昨日 00:00:00 - 23:59:59
export const yesterday = () => {
  // 获取昨日 开始时间
  // 今天开始时间 减去 一天
  const start = moment(startDayTime(new Date())).subtract(1, 'day')

  // 获取昨日 结束时间
  // 今天结束时间 减去 一天
  const end = moment(endDayTime(new Date())).subtract(1, 'day')

  return [start, end]
}

// 获取本周周一 至 本周周日
export const currentWeek = () => {
  // 获取本周是第几周
  const week = moment().week()

  const currentWeekStrat = moment()
    .week(week)
    .startOf('week')
    .format()

  const currentWeekEnd = moment()
    .week(week)
    .endOf('week')
    .format()

  const start = startDayTime(new Date(currentWeekStrat))
  const end = endDayTime(new Date(currentWeekEnd))

  return [start, end]
}

// 获取上周周一 至 上周周日
export const lastWeek = () => {
  // 获取上一周是第几周
  const week = moment().week() - 1

  const lastWeekStrat = moment()
    .week(week)
    .startOf('week')
    .format()

  const lastWeekEnd = moment()
    .week(week)
    .endOf('week')
    .format()

  const start = startDayTime(new Date(lastWeekStrat))
  const end = endDayTime(new Date(lastWeekEnd))

  return [start, end]
}

// 获取某年某个月份第一天和最后一天
export const monthSection = (date: Date, month: number) => {
  const monthSectionStrat = moment(date)
    .month(month)
    .startOf('month')
    .format()

  const monthSectionhEnd = moment(date)
    .month(month)
    .endOf('month')
    .format()

  const start = startDayTime(new Date(monthSectionStrat))
  const end = endDayTime(new Date(monthSectionhEnd))

  return [start, end]
}

// 获取本月第一天 至 本月最后一天
export const currentMonth = () => {
  // 获取当前第几个月
  const month = moment().month()

  return monthSection(new Date(), month)
}

// 获取上月第一天 至 上月最后一天
export const lastMonth = () => {
  // 获取上一个月是第几个月
  const month = moment().month() - 1

  return monthSection(new Date(), month)
}
