import { MutationTree, ActionTree, GetterTree } from 'vuex'
import { getStorage, isEmpty, setStorage } from 'plugin/helper'
import { cloneDeep } from 'lodash'

interface appStateInterface {
  /**
   * 侧边栏状态
   */
  sidebarStatus: boolean
  /**
   * app 组件大小
   */
  appSize: string
  /**
   * 缩放提示
   */
  zoomTips: boolean
  /**
   * app 新手指导
   */
  introStatus: boolean
  /**
   * 可选用户类型选项
   */
  userTypeOptions: Array<any>
}

let state: appStateInterface = {
  sidebarStatus: getStorage('sidebarStatus'),
  appSize: getStorage('appSize'),
  zoomTips: getStorage('zoomTips'),
  introStatus: getStorage('introStatus'),
  userTypeOptions: [
    { label: '超级管理员', value: 1 },
    { label: '分级管理员', value: 2 },
    { label: '普通用户', value: 3 }
  ]
}

let mutations: MutationTree<appStateInterface> = {
  // 切换侧边栏开关
  TOGGLE_SIDEBAR(state: appStateInterface) {
    state.sidebarStatus = !state.sidebarStatus
    setStorage('sidebarStatus', state.sidebarStatus)
  },

  // 设置大小
  SET_APP_SIZE(state: appStateInterface, appSize: string) {
    state.appSize = appSize
    setStorage('appSize', state.appSize)
  },

  // 设置是否需要缩放提示
  SET_ZOOM_TIPS(state: appStateInterface, zoomTips: boolean) {
    state.zoomTips = zoomTips
    setStorage('zoomTips', state.zoomTips)
  },

  // 设置是否需要新手指导
  SET_INTRO_STATUS(state: appStateInterface, introStatus: boolean) {
    state.introStatus = introStatus
    setStorage('introStatus', state.introStatus)
  }
}

let actions: ActionTree<appStateInterface, appStateInterface> = {
  toggleSidebar({ commit }) {
    commit('TOGGLE_SIDEBAR')
  },

  setAppSize({ commit }, appSize: string) {
    commit('SET_APP_SIZE', appSize)
  },

  setZoomTips({ commit }, zoomTips: boolean) {
    commit('SET_ZOOM_TIPS', zoomTips)
  },

  setIntroStatus({ commit }, introStatus: boolean) {
    commit('SET_INTRO_STATUS', introStatus)
  },

  cleanSettings({ commit, dispatch }) {
    dispatch('closeApp')
    commit('SET_APP_SIZE', 'medium')
    commit('SET_ZOOM_TIPS', false)
    commit('SET_INTRO_STATUS', false)
  },

  closeApp({ commit, dispatch }) {
    commit('dictionary/SET_HAS_QUERY_DICTIONARY', false, { root: true })
    commit('user/SET_HAS_QUERY_USER_INFO', false, { root: true })
    dispatch('ws/closeWs', {}, { root: true })
  }
}

let getters: GetterTree<appStateInterface, appStateInterface> = {
  sidebarStatus(state: appStateInterface) {
    return state.sidebarStatus || getStorage('sidebarStatus')
  },

  appSize(state: appStateInterface) {
    return state.appSize || getStorage('appSize')
  },

  zoomTips(state: appStateInterface) {
    return state.zoomTips || getStorage('zoomTips')
  },

  introStatus(state: appStateInterface) {
    return state.introStatus || getStorage('introStatus')
  },

  userTypeOptions(state: appStateInterface, getters: any, store: any) {
    const userType = store.user.userType
    const userTypeOptions = state.userTypeOptions

    return (isFilter: boolean = false) => {
      if (isFilter) {
        // 超管
        if (userType === 1) {
          return userTypeOptions
        }

        // 分级管理员
        if (userType === 2) {
          return userTypeOptions.filter((item) => item.value === 2 || item.value === 3)
        }

        // 普通员工
        if (!userType || userType === 3) {
          return userTypeOptions.filter((item) => item.value === 3)
        }
      }

      return userTypeOptions
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
