import { post } from 'plugin/axios/fetch'

export default {
  /**
   * 获取表单页面列表
   */
  list: (data?: any, config?: requestConfig) => post('/form/page/list', data, config),

  /**
   * 表单页面新增/修改
   */
  update: (data?: any, config?: requestConfig) => post('/form/page/update', data, config),

  /**
   * 表单页面详情
   */
  detail: (data?: any, config?: requestConfig) => post('/form/page/detail', data, config),

  /**
   * 表单页面删除
   */
  updateStatus: (data?: any, config?: requestConfig) => post('/form/page/updateStatus', data, config),

  /**
   * 表单页面复制
   */
  copy: (data?: any, config?: requestConfig) => post('/form/page/copy', data, config),

  /**
   * 修改表单组件
   */
  updateConfig: (data?: any, config?: requestConfig) => post('/form/page/updateConfig', data, config)
}
