import moment from 'moment'
import { desensitization, encodeSmall } from 'plugin/helper'

class InPrototype {
  constructor() {
    this.init()
  }

  init(): void {
    this.strToDate()
    this.numberToDate()
    this.dateObjToDate()
    this.stringDesensitization()
    this.numberDesensitization()
    this.stringEncodeSmall()
    this.numberEncodeSmall()
    this.stringLimit()
  }

  /**
   * 字符串转换时间格式
   * 2019-09-09.format(fmt)
   */
  strToDate(): void {
    String.prototype.format = function(fmt: string = 'YYYY-MM-DD HH:mm:ss'): string {
      return moment(new Date(this as string)).format(fmt)
    }
  }

  /**
   * 数字转换时间格式
   * 12313131323231.format(fmt)
   */
  numberToDate(): void {
    Number.prototype.format = function(fmt: string = 'YYYY-MM-DD HH:mm:ss'): string {
      return moment(new Date(this as number)).format(fmt)
    }
  }

  /**
   * 日期对象转换时间格式
   * new Date().format(fmt)
   */
  dateObjToDate(): void {
    Date.prototype.format = function(fmt: string = 'YYYY-MM-DD HH:mm:ss'): string {
      return moment(new Date(this as Date)).format(fmt)
    }
  }

  /**
   * 脱敏
   * string.desensitization(start, end)
   */
  stringDesensitization(): void {
    String.prototype.desensitization = function(start: number = 4, end: number = 4): string {
      const str: string = this as string
      return desensitization(str, start, end)
    }
  }

  /**
   * 脱敏
   * number.desensitization(start, end)
   */
  numberDesensitization(): void {
    Number.prototype.desensitization = function(start: number = 4, end: number = 4): string {
      const str: string = String(this)
      return desensitization(str, start, end)
    }
  }

  /**
   * 数字转中文小写
   * string.encodeSmall
   */
  stringEncodeSmall(): void {
    String.prototype.encodeSmall = function(): string {
      const num: string = this as string
      return encodeSmall(num)
    }
  }

  /**
   * 数字转中文小写
   * number.encodeSmall
   */
  numberEncodeSmall(): void {
    Number.prototype.encodeSmall = function(): string {
      const num: number = this as number
      return encodeSmall(num)
    }
  }

  /**
   * 字符串切割
   */
  stringLimit(): void {
    String.prototype.limit = function(length: number, suffix: string = '...'): string {
      const str: string = this as string

      if (str.length > length) {
        return `${str.slice(0, length)}${suffix}`
      }

      return str
    }
  }
}

new InPrototype()
