import { isArray, isObject } from './type'

// 全局应用名称 避免冲突
const GLOBAL_NAME = process.env.VUE_APP_GLOBAL_NAME

/**
 * 设置单个 localStorage
 */
export const setStorage = (key: string, value: any, type: string = 'local'): void => {
  const storage = type === 'local' ? window.localStorage : window.sessionStorage

  if (isArray(value) || isObject(value)) {
    storage.setItem(`${GLOBAL_NAME}-${key}`, JSON.stringify(value))
  } else {
    storage.setItem(`${GLOBAL_NAME}-${key}`, value)
  }
}

/**
 * 获取单个 localStorage
 */
export const getStorage = (key: string, type: string = 'local'): any => {
  const storage = type === 'local' ? window.localStorage : window.sessionStorage

  let value: string | null = storage.getItem(`${GLOBAL_NAME}-${key}`)
  let result = null

  if (value) {
    try {
      result = JSON.parse(value)
    } catch (error) {
      result = value
    }
  } else {
    result = value
  }

  return result
}

/**
 * 清除单个 localStorage
 */
export const removeStorage = (key: string): void => {
  localStorage.removeItem(`${GLOBAL_NAME}-${key}`)
}

/**
 * 设置多个 localStorage
 */
export const setMultiStorage = (map: { [k: string]: any }): void => {
  Object.keys(map).forEach((key: string) => setStorage(key, map[key]))
}

/**
 * 获取多个 localStorage
 */
export const getMultiStorage = (array: Array<string>): { [k: string]: any } => {
  const map: { [k: string]: any } = {}
  array.forEach(key => (map[key] = getStorage(key)))
  return map
}

/**
 * 清除多个 localStorage
 */
export const removeMultiStorage = (array: Array<string>): void => {
  array.forEach((key: string) => removeStorage(key))
}
