
import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({
  name: 'comDialog'
})
export default class comDialog extends Vue {
  // 取消按钮文字
  @Prop({ type: String, default: '取 消' }) cancelBtnText!: string
  // 确认按钮文字
  @Prop({ type: String, default: '确 定' }) determineBtnText!: string
  // 是否显示取消按钮
  @Prop({ type: Boolean, default: true }) showCancelBtn!: boolean
  // 是否显示确认按钮
  @Prop({ type: Boolean, default: true }) showDetermineBtn!: boolean
  // 是否禁用取消按钮
  @Prop({ type: Boolean, default: false }) cancelBtnDisabled!: boolean
  // 是否禁用确认按钮
  @Prop({ type: Boolean, default: false }) determineBtnDisabled!: boolean
  // 取消前
  @Prop({ type: Function }) beforeCancel!: Function
  // 确认前
  @Prop({ type: Function }) beforeDetermine!: Function

  // 是否显示底部
  @Prop({ type: Boolean, default: true }) showFooter!: string

  loading: boolean = false
  visible: boolean = false

  cancelLoading() {
    this.loading = false
  }

  open() {
    this.visible = true
    this.cancelLoading()
  }

  close() {
    this.visible = false
    this.cancelLoading()
  }

  cancel() {
    this.loading = true

    this.beforeCancel ? this.beforeCancel(this.close, this.cancelLoading) : this.close()
  }

  determine() {
    this.loading = true

    this.beforeDetermine ? this.beforeDetermine(this.close, this.cancelLoading) : this.close()
  }
}
