
import { Vue, Component, Prop, Watch } from 'vue-property-decorator'

import insideTabPane from './pane.vue'

@Component({
  name: 'insideTabs',
  components: {
    insideTabPane
  }
})
export default class insideTabs extends Vue {
  @Prop({ type: [Number, String] }) value!: string | number

  @Prop({ type: Array, default: () => [] }) options!: Array<any>

  currentName: string | number = this.value

  @Watch('value', { immediate: true })
  valueHandler() {
    this.currentName = this.value
  }

  updateName(name: string | number) {
    this.currentName = name
    this.$emit('input', this.currentName)
    this.$emit('change', this.currentName)
  }

  created() {
    this.$on('tab-name-update', this.updateName)
  }

  beforeDestroy() {
    this.$off('tab-name-update')
  }
}
