import introJs from 'intro.js'
import steps from './steps'
import { store } from '@/store'
import 'intro.js/introjs.css'

export const introStart = () => {
  const intro = introJs()

  intro.setOptions({
    skipLabel: '跳过',
    doneLabel: '完成',
    prevLabel: '上一步',
    nextLabel: '下一步',
    showStepNumbers: false,
    exitOnOverlayClick: false,
    steps: steps
  })

  intro.oncomplete(() => {
    store.dispatch('app/setIntroStatus', true)
  })

  intro.onexit(() => {
    store.dispatch('app/setIntroStatus', true)
  })

  intro.start()
}
