
import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({
  name: 'comPageWrapper'
})
export default class comPageWrapper extends Vue {
  @Prop({ type: Object }) bodyStyle!: object

  insideBodyStyle: { [k: string]: string } = {
    'overflow-y': 'scroll',
    height: '100%',
    ...this.bodyStyle
  }
}
