
import { Vue, Component, Watch, Prop, Ref } from 'vue-property-decorator'

import draggable from 'vuedraggable'

@Component({
  name: 'comNewTags',
  components: {
    draggable
  }
})
export default class comNewTags extends Vue {
  @Ref('saveTagInput') saveTagInputRef!: any

  @Prop({ type: Array }) value!: Array<string>
  @Prop({ type: Number, default: Infinity }) max!: number
  @Prop({ type: Number, default: 0 }) min!: number
  @Prop({ type: String }) itemMaxlength!: string
  @Prop({ type: Boolean, default: false }) isOnly!: boolean

  tags: Array<string> = []
  inputNewTagVisible: boolean = false
  newTag: string = ''

  @Watch('value', { immediate: true, deep: true })
  valueHandler() {
    this.tags = this.value
  }

  // Detail see : https://github.com/RubaXa/Sortable/issues/1012
  setData(dataTransfer: any) {
    dataTransfer.setData('Text', '')
  }

  handleClose(tag: string) {
    this.tags.splice(this.tags.indexOf(tag), 1)
    this.tagsChange()
  }

  showNewTagInput() {
    this.inputNewTagVisible = true
    this.$nextTick(() => {
      this.saveTagInputRef.$refs.input.focus()
    })
  }

  handleInputConfirm() {
    let newTag = this.newTag

    const isRepeat = this.isOnly && this.tags.includes(newTag)

    if (!isRepeat) {
      newTag && this.tags.push(newTag)
    } else {
      this.$message.warning('已存在此选项, 不可重复添加~')
    }

    this.inputNewTagVisible = false
    this.newTag = ''
    this.tagsChange()
  }

  tagsChange() {
    this.$emit('input', this.tags)
    this.$emit('change', this.tags)
  }
}
