import clipboard from 'clipboard-polyfill'

import { isEmpty } from './type'
import { fail } from 'plugin/toast'

const nzh = require('nzh')

/**
 * 是否是外链
 */
export const isExternal = (path?: string): boolean => /^(https?:|mailto:|tel:)/.test(path || '')

/**
 * 数字转中文小写
 */
export const encodeSmall = (num: number | string) => {
  if (!String(num)) {
    return ''
  }
  return nzh.cn.encodeS(num)
}

/**
 * 脱敏
 */
export const desensitization = (str: string, start: number, end: number): string => {
  if (!str) {
    return ''
  }
  // 如果字符长度 小于或者等于 开始+结束位置 那就全脱敏
  if (str.length <= start + end) {
    start = 0
    end = 0
  }

  const startStr = str.substring(0, start)
  const endStr = str.substring(str.length - end, str.length)
  const middleStr = str.substring(start, str.length - Math.abs(end)).replace(/[\s\S]/gi, '*')
  return `${startStr}${middleStr}${endStr}`
}

/**
 * 保留小数点后几位
 * string | toFixed(num)
 * number | toFixed(num)
 */
export const toFixed = (val: string | number, num: number = 2): string => {
  const value: string | number = isEmpty(val) ? 0 : val
  return parseFloat(String(value)).toFixed(num)
}

/**
 * 拷贝文字到剪切板
 */
export const clipboardText = (text: string): Promise<true> => {
  return new Promise(async (resolve, reject) => {
    try {
      await clipboard.writeText(text)
      resolve(true)
    } catch (error) {
      fail('复制到剪切板失败~')
      reject(error)
    }
  })
}

/**
 * create Promise
 */
export const createPromise = () => {
  let _resolve!: (value: any) => void
  let _reject!: (reason?: any) => void

  const promise = new Promise((resolve, reject) => {
    _resolve = resolve
    _reject = reject
  })

  return {
    promise: promise,
    resolve: _resolve,
    reject: _reject
  }
}
