
import { insideNavbar, insideTagView, insideMain } from 'components/core'
import { Vue, Component } from 'vue-property-decorator'
import { Getter, Action } from 'vuex-class'
import { introStart } from 'plugin/intro'

@Component({
  name: 'layout',
  components: {
    insideNavbar,
    insideMain,
    insideTagView
  }
})
export default class layout extends Vue {
  @Getter('app/introStatus') introStatus!: boolean

  mounted() {
    setTimeout(() => {
      if (!this.introStatus) {
        introStart()
      }
    }, 0)
  }
}
