
import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({
  name: 'insideTabPane'
})
export default class insideTabPane extends Vue {
  @Prop({ type: [Number, String] }) name!: string | number
  @Prop({ type: String }) label!: string

  get isAcitve() {
    return (this.$parent as any).currentName === this.name
  }

  get paneClass() {
    return this.isAcitve ? ['inside-tab__pane', 'tab-pane__active'] : ['inside-tab__pane']
  }

  tabPaneClick() {
    this.$parent?.$emit('tab-name-update', this.name)
  }
}
