import layout from 'views/layout/index.vue'

const rootRoutes: Array<any> = [
  {
    path: '/',
    component: layout,
    children: [
      {
        path: '',
        redirect: 'pages/setting'
      },
      {
        path: 'pages/setting',
        name: 'pagesSetting',
        meta: { title: '编辑', cache: true },
        component: () => import(/* webpackChunkName: "pages" */ 'views/pages-manage/setting.vue')
      }
    ]
  }
]

export default rootRoutes
